import { Injectable } from '@angular/core';
import { CanActivate } from '@angular/router';
import { AppStateService } from '@c8y/ngx-components';
import { Permissions } from '@c8y/ngx-components';

@Injectable()
export class SimGuard implements CanActivate {
  private static readonly applicationName = 'connectivity';
  private showSimProvider: Promise<boolean>;

  constructor(private permissions: Permissions, private appState: AppStateService) {}

  canActivate(): Promise<boolean> {
    if (this.showSimProvider === undefined) {
      const hasRole: Promise<boolean> = Promise.resolve(
        this.permissions.hasRole('ROLE_CONNECTIVITY_READ')
      );

      this.showSimProvider = hasRole.then(
        (hasPermissions: boolean) =>
          hasPermissions && this.appState.isApplicationAvailable(SimGuard.applicationName)
      );
    }

    return this.showSimProvider;
  }
}
