import { CanActivate, Router } from '@angular/router';
import { Tab, TabFactory, toObservable } from '@c8y/ngx-components';
import { Observable } from 'rxjs';
import { map } from 'rxjs/operators';

export abstract class AbstractConnectivityTabFactory implements TabFactory {
  constructor(protected router: Router, protected guard: CanActivate) {}

  get() {
    return this.router.url.match(/connectivitySettings/g) ? this.tab() : [];
  }

  tab(): Tab | Observable<Tab> {
    return toObservable(
      this.guard.canActivate(
        this.router.routerState.snapshot.root,
        this.router.routerState.snapshot
      )
    ).pipe(map(canActivate => (canActivate ? this.config() : null)));
  }

  protected abstract config(): Tab;
}
