import { Injectable } from '@angular/core';
import { CanActivate } from '@angular/router';
import { AppStateService } from '@c8y/ngx-components';

@Injectable()
export class ActilityGuard implements CanActivate {
  private static readonly applicationName = 'actility';
  private showActilityProvider: Promise<boolean>;

  constructor(private appState: AppStateService) {}

  canActivate(): Promise<boolean> {
    if (this.showActilityProvider === undefined) {
      this.showActilityProvider = this.appState.isApplicationAvailable(
        ActilityGuard.applicationName
      );
    }

    return this.showActilityProvider;
  }
}
