import { Injectable } from '@angular/core';
import { gettext, NavigatorNode, NavigatorNodeFactory, toObservable } from '@c8y/ngx-components';
import { map } from 'rxjs/operators';
import { AbstractConnectivityTabFactory } from './abstract-connectivity-tab.factory';

@Injectable()
export abstract class AbstractConnectivityNodeFactory implements NavigatorNodeFactory {
  protected readonly connectivityNode: NavigatorNode = new NavigatorNode({
    parent: gettext('Settings'),
    label: gettext('Connectivity'),
    path: 'connectivitySettings',
    icon: 'plug',
    preventDuplicates: true
  });

  constructor(protected tabFactory: AbstractConnectivityTabFactory) {}

  get() {
    return toObservable(this.tabFactory.tab()).pipe(
      map(tab => {
        return !!tab ? [this.connectivityNode] : [];
      })
    );
  }
}
