import { Injectable } from '@angular/core';
import { CanActivate } from '@angular/router';
import { AppStateService } from '@c8y/ngx-components';

@Injectable()
export class ImpactGuard implements CanActivate {
  private static readonly applicationName = 'impact';
  private showImpactProvider: Promise<boolean>;

  constructor(private appState: AppStateService) {}

  canActivate(): Promise<boolean> {
    if (this.showImpactProvider === undefined) {
      this.showImpactProvider = this.appState.isApplicationAvailable(ImpactGuard.applicationName);
    }

    return this.showImpactProvider;
  }
}
