import { Injectable } from '@angular/core';
import { CanActivate } from '@angular/router';
import { AppStateService } from '@c8y/ngx-components';

@Injectable()
export class SigfoxGuard implements CanActivate {
  private static readonly applicationName = 'sigfox-agent';
  private showSigfoxProvider: Promise<boolean>;

  constructor(private appState: AppStateService) {}

  canActivate(): Promise<boolean> {
    if (this.showSigfoxProvider === undefined) {
      this.showSigfoxProvider = this.appState.isApplicationAvailable(SigfoxGuard.applicationName);
    }

    return this.showSigfoxProvider;
  }
}
