import { Injectable } from '@angular/core';
import { gettext, NavigatorNode, NavigatorNodeFactory, toObservable } from '@c8y/ngx-components';
import { map } from 'rxjs/operators';
import { ImpactTabFactory } from './impact-tab.factory';

@Injectable({ providedIn: 'root' })
export class ImpactNodeFactory implements NavigatorNodeFactory {
  private readonly connectivityNode: NavigatorNode = new NavigatorNode({
    parent: gettext('Settings'),
    label: gettext('Connectivity'),
    path: 'connectivitySettings',
    icon: 'plug',
    preventDuplicates: true
  });

  constructor(private tabFactory: ImpactTabFactory) {}

  get() {
    return toObservable(this.tabFactory.tab()).pipe(
      map(tab => {
        return !!tab ? [this.connectivityNode] : [];
      })
    );
  }
}
